import React from 'react';
import Reveal from "reveal.js";
import {TitleSlide} from "components/slides/Title";
import {useTranslation} from "react-i18next";

const Disasters = () => {

    const {t: translate} = useTranslation()

    React.useEffect(() => {
        let deck = new Reveal({
            backgroundTransition: 'slide',
            transition: 'slide',
            display: 'flex',
            hash: true,
            respondToHashChanges: true
        })
        deck.initialize();
    }, [])

    return (
        <>
            <div className="reveal !h-screen !w-screen">
                <div className="slides" data-transition="slide">
                    <TitleSlide>
                        <>
                            <h1>{translate('slides.lifeguarding.disasters.title')}</h1>
                            <h2>{translate('slides.lifeguarding.disasters.subtitle')}</h2>
                        </>
                    </TitleSlide>
                    <TitleSlide>
                        <>
                            <h1>Agenda</h1>
                            <ul className="text-left space-y-6 list-disc marker:!text-indigo-500">
                                <li className="text-white">Organisatie hulpverlening</li>
                                <li className="text-white">Gecordineerde Regionale <br/> Incidentbestrijdingsprocedure
                                </li>
                                <li className="text-white">Grootschalige Geneeskundige <br/> Bijstand</li>
                                <li className="text-white">Grootschalige inzet politie</li>
                                <li className="text-white">Nationale Reddingsvloot</li>
                            </ul>
                        </>
                    </TitleSlide>
                </div>
            </div>
        </>)
}

export {Disasters}